.nav-bar{
    display: flex;
    position: fixed;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    height: 100vh;
    padding: rem(5);
    @include principalColor();
    border-radius: 5px;
    box-shadow: 5px 10px 30px #000;
}
.nav-options{
    text-decoration: none;
    color: $colorFont;
    font-family: $fontTitle;
    font-weight: bold;
}
.icon{
    width: rem(100);
    height: rem(100);
}

